body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  
}
#Landing-Section {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    color: #525252;
    background: #fafafa;
    text-align: center;
}

#Landing-Section h1 {
    margin: 0;
    font-size: 450%;
    font-weight: 100;
}

#Landing-Section h3 {
    color: #f4645f;
    font-size: 150%;
    font-weight: 400;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

#Social-Container {
    font-size: 200%;
    display: flex;
    width: 60vw;
    justify-content: center;
    -webkit-animation-delay: 1.3s;
            animation-delay: 1.3s;
}

#Social-Container a {
    padding: 0 3.5%;
    margin-top: 1%;
    color: #525252;
}

#Social-Container a:hover, #Social-Container a:active {
    transition-duration: 130ms;
    color: #3dbcb1;
}
#Navbar-Section {
    position:  fixed;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background: #fafafa;
    width: 100vw;
    height: 8vh;
    align-items: center;
}

#Navbar-Section a {
    text-decoration: none;
    font-weight: 200;
    color: #f4645f;
    margin-right: 3%;
    font-size: 140%;
    transition-duration: 230ms;
}

#Navbar-Section a:hover {
    transition-duration: 230ms;
    text-decoration: underline;
    color: #f4645f;
}
